import { Component, OnInit, NgZone } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { ApplicationService } from "../../shared/electron/application.service";
import { TranslateService } from "ng2-translate";
const marked = require('marked');

const fs = (<any>global).nodeRequire('fs');

@Component({
    moduleId: module.id,
    selector: 'changelog',
    templateUrl: 'changelog.component.html',
    styleUrls: ['changelog.component.css']
})
export class ChangeLogComponent implements OnInit {

    private content: string;

    constructor(private titleService: Title,
        private applicationService: ApplicationService,
        private translate: TranslateService,
        private zone: NgZone) { }

    ngOnInit() {

        this.titleService.setTitle(this.translate.instant("app.window.changelog.title"));

        marked.setOptions({
            gfm: true,
            tables: true,
            breaks: true,
            sanitize: true,
            smartLists: true,
            smartypants: true
        });

        fs.readFile(this.applicationService.appPath + '/CHANGELOG.md', { encoding: 'utf-8' }, (err: any, data: any) => {
            this.zone.run(() => {
                this.content = marked(data);
            });
        });
    }

}
